.row{
    margin-top: 13px;
  
    margin-left: 15px;
    color: aliceblue;
}

.row_poster {
    object-fit: contain;
     width: 100%;
    max-height: 70px;
    margin-right: 12px;
    transition: transform 200ms;
   
}

.row_poster:hover{
    transform: scale(1.15);
    opacity: 1;
}

.row_posters::-webkit-scrollbar{
    display: none;
}

.row_posters{
    display: flex;
    overflow-y: hidden;
    overflow-x: scroll;
    padding:0px;
}

.rowLargeposter{
    max-height: 180px;
}